@import "../scss/utils";

.list_todo {
  margin-top: 10px;
}

.no_data {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 200px;
  margin-top: 10px;
  font-size: 16px;
  color: #999;
  border-radius: $list-todo-border-radius;
  background-color: rgba(0, 0, 0, 0.03);
}

.item_todo {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 15px;
  margin-top: -1px;
  border: 1px solid #ccc;

  &:first-child {
    border-top-left-radius: $list-todo-border-radius;
    border-top-right-radius: $list-todo-border-radius;
  }

  &:last-child {
    border-bottom-left-radius: $list-todo-border-radius;
    border-bottom-right-radius: $list-todo-border-radius;
  }

  &:hover {
    position: relative;
    border-color: $G-primary-color;
    box-shadow: 0 0 15px 0 rgba($G-primary-color, 0.2);

    .label {
      font-weight: 700;
      color: $G-primary-color;
    }

    .icon_checkbox {
      &,
      &:before {
        border-color: $G-primary-color;
      }
    }

    &.is_checked {
      background-color: rgba($G-primary-color, 0.05);
    }

    .icon_delete {
      background-color: $G-primary-color;
    }
  }
}

.is_checked {
  background-color: rgba(0, 0, 0, 0.03);

  .label {
    color: #aaa;
    text-decoration: line-through;
  }

  .icon_checkbox {
    &:before {
      content: "";
      display: block;
      position: absolute;
      top: 6px;
      left: 5px;
      width: 9px;
      height: 5px;
      border-left: 1px solid #aaa;
      border-bottom: 1px solid #aaa;
      transform: rotate(-45deg);
      transform-origin: center;
    }
  }
}

.input_checkbox {
  @include blind();
}

.label {
  width: 100%;
  padding: 15px 0;
  font-size: 14px;
  line-height: 1.44;
  color: #222;
  cursor: pointer;

  @include ellipsis();

  .icon_checkbox {
    display: inline-block;
    position: relative;
    width: 20px;
    height: 20px;
    margin-right: 10px;
    border: 1px solid #aaa;
    border-radius: 50%;
    vertical-align: middle;
  }

  .text {
    vertical-align: middle;
  }
}

.button_delete {
  margin-right: -10px;
  padding: 10px;
}

.icon_delete {
  background-color: #ccc;
  border-radius: 50%;
  @include iconCancel(
    $size: 8px,
    $thick: 1px,
    $color: #fff,
    $padding: 6px,
    $display: block,
    $position: relative
  );
}
