@charset "utf-8";

@import "../utils";

/* common.scss */
:global {
  body {
    background-color: $G-body-background-color;
  }

  legend,
  .blind {
    overflow: hidden;
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    font-size: medium;
    line-height: normal;
    white-space: nowrap;
  }
}
