@charset "utf-8";

@mixin blind() {
	overflow: hidden;
	position: absolute;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
	font-size: medium;
	line-height: normal;
	white-space: nowrap;
}

@mixin afterClear() {
	content: '';
	display: block;
	clear: both;
}

@mixin ellipsis($lines: 1, $display: inline-block, $width: null, $max-width: null, $overflow: hidden) {
	overflow: $overflow;
	width: $width;
	max-width: $max-width;
	text-overflow: ellipsis;
	@if ($lines <= 1) {
		display: $display;
		white-space: nowrap;
		word-wrap: normal;
	} @else {
		display: $display;
		display: -webkit-box;
		-webkit-line-clamp: $lines;
		-webkit-box-orient: vertical;
	}
}

@mixin setBorderMask($width: 1px, $color: #000, $opacity: 0.05) {
	&::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		border: $width solid rgba($color, $opacity);
		@content;
	}
}

@mixin placeholder() {
	&::-webkit-input-placeholder {
		// Chrome, Safari, Edge
		@content;
	}
	&::-moz-placeholder {
		// FF 19+
		@content;
	}
	&:-ms-input-placeholder {
		// IE 10+
		@content;
	}
	&:-moz-placeholder {
		// FF 18-
		@content;
	}
}