@import "../scss/utils";

$area-input-border-radius: 10px;
$area-input-line-height: 46px;

.area_input {
  display: flex;
  margin-top: 10px;

  .input_todo {
    flex: 1 0 auto;
    padding: 0 15px;
    font-size: 16px;
    font-weight: 700;
    line-height: $area-input-line-height;
    color: #000;
    border: 1px solid #ccc;
    border-top-left-radius: $area-input-border-radius;
    border-bottom-left-radius: $area-input-border-radius;
    outline: transparent;

    &:focus {
      border-color: $G-primary-color;
    }

    @include placeholder {
      font-weight: 400;
      color: #acacac;
    }
  }

  .button_summit {
    display: inline-block;
    padding: 0 15px;
    font-size: 16px;
    line-height: $area-input-line-height;
    color: #fff;
    background-color: $G-primary-color;
    border: 1px solid $G-primary-color;
    border-top-right-radius: $area-input-border-radius;
    border-bottom-right-radius: $area-input-border-radius;
    box-sizing: border-box;
  }
}
