@charset "utf-8";

@import "../utils";

/* base.scss */
body,
p,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
th,
td,
form,
fieldset,
legend,
input,
textarea,
button,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
menu,
nav,
section,
audio,
video,
blockquote {
  margin: 0;
  padding: 0;
}

header,
section,
footer,
aside,
nav,
main,
article,
figure {
  display: block;
  /* IE 브라우저에서 지원되지 않는 시맨틱 태그, 블록 레벨 요소로 동작되지 않는 부분 대응 */
}

body,
input,
textarea,
select,
table,
button,
code {
  font-family: $G-font-family-base;
  font-size: $G-font-size-base;
  line-height: $G-line-height-base;
}

body {
  position: relative;
  background-color: $G-body-background-color;
  word-wrap: break-word;
  word-break: keep-all;
  -webkit-text-size-adjust: none;
  /* iOS에서 터치 이벤트가 적용된 요소에 의도지 않게 강조표시가 되는 부분을 막기 위해 사용.
	  ※ https://developer.mozilla.org/en-US/docs/Web/CSS/-webkit-tap-highlight-color */
  -webkit-tap-highlight-color: transparent;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

img,
fieldset {
  border: 0;
}

img {
  vertical-align: top;
}

ul,
ol {
  list-style: none;
}

em,
address,
cite,
dfn {
  font-style: normal;
}

abbr {
  text-decoration: none;
}

a,
label,
button,
input[type="checkbox"],
input[type="radio"] {
  color: inherit;
  text-decoration: none;
  /* a 요소에만 터치시 강조표시 되도록 하기 위해 사용. iOS 기본값으로 색상 적용. */
  -webkit-tap-highlight-color: rgba(26, 26, 26, 0.301961);
}

table {
  border-collapse: collapse;
}

hr {
  border: 0;
  margin: 0;
  padding: 0;
}

textarea {
  overflow: auto;
  border: 1px solid #bcbcbc;
  resize: none;
  -webkit-overflow-scrolling: touch;
}

button {
  overflow: visible;
  border: none;
  border-radius: 0;
  background: none;
  cursor: pointer;
  -webkit-user-select: none;
}

button[disabled] {
  cursor: default;
}

button,
[role="button"] {
  cursor: pointer;
}

input[type="image"],
input[type="text"],
input[type="submit"],
input[type="password"] {
  border-radius: 0;
}

input[type="text"]::-ms-clear {
  display: none;
}

input[type="password"]::-ms-reveal {
  display: none;
}
